.login-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #ff7043 0%, #ffe0b2 100%);
  animation: bgFadeIn 1s ease;
}

@keyframes bgFadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.login-container {
  background: #fff;
  padding: 2.5rem 2rem 2rem 2rem;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(34,34,59,0.16);
  min-width: 340px;
  animation: popIn 0.7s cubic-bezier(.68,-0.55,.27,1.55);
  transition: opacity 0.5s;
}

@keyframes popIn {
  0% { transform: scale(0.8) translateY(60px); opacity: 0; }
  80% { transform: scale(1.05) translateY(-8px); opacity: 1; }
  100% { transform: scale(1) translateY(0); opacity: 1; }
}

.login-container.fade-out {
  opacity: 0;
  pointer-events: none;
}

.login-container h2 {
  text-align: center;
  margin-bottom: 1.8rem;
  color: #ff7043;
  font-weight: 700;
}

.login-form label {
  display: block;
  margin-bottom: 0.3rem;
  font-weight: 600;
  color: #2d2d2d;
}

.login-form input {
  width: 100%;
  padding: 0.7rem 0.9rem;
  margin-bottom: 1.2rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
}

.login-form button {
  width: 100%;
  padding: 0.9rem;
  background: #ff7043;
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s;
}

.login-form button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.login-form button:hover:not(:disabled) {
  background: #2d2d2d;
}

.error {
  color: #e53935;
  margin-bottom: 1rem;
  font-weight: 600;
  text-align: center;
}
