.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.empty-cart {
  text-align: center;
  padding: 3rem;
}

.continue-shopping {
  display: inline-block;
  background: #ff7043;
  color: white;
  padding: 0.8rem 2rem;
  text-decoration: none;
  border-radius: 4px;
  margin-top: 1rem;
}

.cart-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  margin-top: 2rem;
}

.cart-items {
  background: white;
  border-radius: 8px;
  padding: 1.5rem;
}

.cart-item {
  display: flex;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid #eee;
}

.cart-item:last-child {
  border-bottom: none;
}

.item-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 4px;
  margin-right: 1rem;
}

.item-details {
  flex-grow: 1;
}

.item-details h3 {
  margin: 0 0 0.5rem 0;
  color: #333;
}

.item-price {
  color: #ff7043;
  font-weight: 600;
  margin: 0.5rem 0;
}

.quantity-controls {
  margin: 0.5rem 0;
}

.quantity-input {
  width: 60px;
  padding: 0.3rem;
  margin-left: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.remove-btn {
  background: #ff6b6b;
  color: white;
  border: none;
  padding: 0.4rem 0.8rem;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 0.5rem;
}

.item-total {
  font-weight: 600;
  color: #333;
  margin-left: 1rem;
}

.cart-summary {
  background: white;
  border-radius: 8px;
  padding: 1.5rem;
  height: fit-content;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.8rem;
}

.summary-row.total {
  border-top: 1px solid #eee;
  padding-top: 0.8rem;
  font-size: 1.2rem;
}

.cart-actions {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1.5rem;
}

.clear-cart, .checkout-btn {
  padding: 0.8rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  font-weight: 600;
}

.clear-cart {
  background: #f1f1f1;
  color: #333;
}

.checkout-btn {
  background: #ff7043;
  color: white;
}

.nav-links .active {
  font-weight: 600;
  color: #ff7043;
}
